body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
VARIABLES, RE-USEUABLE STYLES
*/
html {
  --primary-yellow: #ffff0080;
  --primary-blue: #0000ff36;
  --hover-yellow: #ffff00fa;
  --hover-blue: #0000ffa6;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html,
body {
  margin: 0;
}

#root {
    padding-left: 10px;
}

#root {
  display: grid;
  grid-template-areas: "filter image"
                       "date image";
  grid-template-columns: 187px auto;
  grid-template-rows: 294px auto;
}

.filter {
  grid-area: filter;
}

.date {
  grid-area: date;
  padding-top: 0 !important;
  margin-top: 0 !important;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.responsive {
  grid-area: image;
}

.title {
  margin: 0;
  font-weight: 100;
  border-bottom: 1px solid;
  letter-spacing: 3px;
  padding-bottom: 2px;
  margin-bottom: 10px;
  text-align: center;
}

.timeBtnList,
.imageContainerList {
  margin: 0;
  list-style-type: none;
}

#root > * {
  margin: 10px 10px 10px 0;
  padding: 10px;
  border: 1px solid black;
  background-color: #00000008;
}

.column {
  flex-direction: column;
}

.horizontal-bar {
   width: 50px;
   margin: auto;
   border-top: 1px solid black;
}

.cursorPointer {
  cursor: pointer;
}

ol {
  padding-left: 0;
}

.flex,
.column {
  display: flex;
}

.flex-grow-1,
.responsive {
  flex-grow: 1;
}

.w-400 {
  width: 385px;
}

.w-154 {
  width: 154px;
}
.table {
  display: table;
}

.table > * {
  display: table-cell;
  vertical-align: middle;
}




/*
IMAGE CONTAINER LIST, IMAGE CONTAINER
*/
.imageContainerList {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  flex-direction: row;
  flex-wrap: wrap;
}

.imageContainer,
.timeBtnList,
.imageContainerList,
.relative {
  position: relative;
}

.imageContainerList .column > h1 {
  margin: 0;
  font-size: 16px;
  font-weight: 100;
  align-items: center;
  color: white;
  background-color: black;
  padding: 5px 10px;
}

.imageContainerList .column > h1 > div {
  flex-grow: 1;
}

.imageContainerList > li {
  margin: 3px 10px 8px 5px ;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}





/*
TIMESTAMP, PREDICTION, BLUE & YELLOW BORDERS 
*/
.timestamp {
  border-radius: 10px;
  font-size: 12px;
  width: 144px;
  height: 30px;
  margin: 5px 0px;
  margin-left: 0;
}

.timeBtnList {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
}

.timestamp > div {
  text-align: center;
}

.prediction {
  position: absolute;
  border: 3px solid black;
}

.am-btn {
  background-color: #ffff0080;
  background-color: var(--primary-yellow);
}

.timestamp:hover.am-btn,
.open.am-btn {
  background-color: #ffff00fa;
  background-color: var(--hover-yellow);
}

.am-border {
  border-color: #ffff00fa;
  border-color: var(--hover-yellow);
}

.pm-btn {
  background-color: #0000ff36;
  background-color: var(--primary-blue);
}

.timestamp:hover.pm-btn,
.open.pm-btn {
  color: white;
  background-color: #0000ffa6;
  background-color: var(--hover-blue);
}

.pm-border {
  border-color: #0000ffa6;
  border-color: var(--hover-blue);
}






/*
SCORE CONTAINER
*/
.scoreContainer {
  opacity: 0;
  position: absolute;
  right: -135px;
  width: 130px;
  background-color: white;
  top: -3px;
  z-index: 1;
  border: 1px solid black;
  text-align: center;
}

.prediction:hover .scoreContainer {
  opacity: 1;
}

.scoreContainer h1 {
  font-size: 13px;
  margin: 0;
  font-weight: 400;
  padding-top: 5px;
}

.scoreContainer ul {
  list-style-type: none;
  padding: 0 0 5px 13px;
}

.scoreContainer li div {
  padding: 5px;
  white-space: nowrap;
  text-align: left;
  font-weight: 100;
}

.scoreContainer li > div:nth-child(2) {
  flex-grow: 1;
  text-align: right;
}

.scoreContainer div {
  font-size: 12px;
}

/*
FILTER
*/

.filter div {
  font-weight: 100;
}

.filter > div {
  padding-bottom: 20px;
}

.filter .table div {
  width: 63px;
}

.filter input[type="text"],
.filter input[type="number"] {
  width: 100%;
  font: 100 13px sans-serif;
  text-align: center;
}


/*
CHECKMARK
*/

.checkmark {
  opacity: 0;
  position: absolute;
  left: -10px;
  width: 12.5px;
  height: 12.5px;
  fill: #136f17;
  top: 7px;
}

.scoreContainer .relative:hover .checkmark {
  opacity: 1;
}

.open.checkmark {
  opacity: 1;
}
